import { useGetTokenBalance } from '@cetus/hooks/src/useTokenBalance'
import { Token } from '@cetus/types'
import { symbolDataDisplayProcessing } from '@cetus/utils'
import { Box } from '@chakra-ui/react'

type BalanceValueProps = {
  tokenInfo: Token
  balance?: string
}
export default function BalanceValue(props: BalanceValueProps) {
  const { balanceInfo: receiveBalance } = useGetTokenBalance(props.tokenInfo)

  return <Box fontSize="14px">{symbolDataDisplayProcessing(receiveBalance?.balanceFormat, '', props.tokenInfo.decimals)}</Box>
}
