import useGetTokenSource from '@cetus/hooks/src/useGetTokenSource'
import { useAccountStore } from '@cetus/stores'
import useTokenSelectStore from '@cetus/stores/src/useTokenSelectStore'
import { Token } from '@cetus/types'
import { Icon } from '@cetus/ui-kit'
import { cancelBubble, d, symbolDataDisplayProcessing } from '@cetus/utils'
import { Box, Center, Flex, Text } from '@chakra-ui/react'
import { useEffect, useState } from 'react'
import CetusTooltip from '../CetusTooltip'
import SingleTokenInfo from '../SingleTokenInfo'
import BalanceValue from './BalanceValue'
import TokenGoplusModal from './TokenGoplusModal'

type TokenItemProps = {
  token: Token
  onSelectToken: (tokenInfo: Token) => void
  isDisableSelectTokenItem: boolean
  lastSelectedToken: string
  currentTab: string
  delImportTokenList: (tokens: Token[]) => void
  addUserCollectToken: (tokens: Token) => void
  delUserCollectToken: (tokens: Token) => void
  userCollectObj: Record<string, Token>
  isShowCollectListBox: boolean
  isShowTokenListTab: boolean
  onGoplusTokenClick?: (tokenInfo: Token) => void
  onClose: () => void
  isNotAllowClick?: boolean
  isHideDelIcon?: boolean
}

const TokenItem = ({
  token,
  onSelectToken,
  isDisableSelectTokenItem,
  lastSelectedToken,
  currentTab,
  delImportTokenList,
  addUserCollectToken,
  delUserCollectToken,
  userCollectObj,
  isShowCollectListBox,
  isShowTokenListTab,
  onGoplusTokenClick,
  onClose,
  isNotAllowClick = false,
  isHideDelIcon = false
}: TokenItemProps) => {
  const { isShowImportBtn, setTokenModalOpacity } = useTokenSelectStore()
  // const [canCollect, setCanCollect] = useState(true)
  // useEffect(() => {
  //   console.log('🚀🚀🚀 ~ userCollectObj:', userCollectObj)
  //   if (
  //     !(
  //       userCollectObj &&
  //       userCollectObj[token.coin_type] &&
  //       userCollectObj[token.coin_type].suggested_priority &&
  //       userCollectObj[token.coin_type].suggested_priority[0]
  //     )
  //   ) {
  //     setCanCollect(true)
  //   } else {
  //     setCanCollect(false)
  //   }
  // }, [userCollectObj, token])

  const { currentAccount } = useAccountStore()

  const { getTokenSource, getTrustTokenSource } = useGetTokenSource()
  const [isShowWarningIcon, setIsShowWarningIcon] = useState(false)
  useEffect(() => {
    if (token) {
      const tokenSource = getTokenSource([token])
      if (tokenSource && tokenSource.length > 0) setIsShowWarningIcon(true)
    }
  }, [token])
  const [isShowGoplusModal, setIsShowGoplusModal] = useState(false)
  return (
    <Flex
      minH="64px"
      justifyContent="space-between"
      alignItems="center"
      cursor={(token.coin_type == lastSelectedToken && isDisableSelectTokenItem) || isNotAllowClick ? 'not-allowed' : 'pointer'}
      userSelect="none"
      p="0 16px"
      marginTop="8px"
      _hover={{
        bg: 'primary_opacity.10',
        borderRadius: '14px'
      }}
      opacity={token.coin_type == lastSelectedToken && isDisableSelectTokenItem ? '0.5' : '1'}
      onClick={() => {
        if (!isNotAllowClick) {
          onSelectToken(token)
        }
      }}
    >
      <SingleTokenInfo
        symbolFontWeight="600"
        token={token}
        haveAddress={true}
        imgBoxStyle={{ w: '32px', h: '32px' }}
        symbolFontSize="14px"
        symbolEllipsesDecimals={20}
        nameEllipsesDecimals={28}
        warningIcon={{ isNeedShow: currentTab !== 'Imported' && isShowWarningIcon }}
      />
      <Flex alignItems="center">
        {currentAccount?.address && (
          <Box textAlign="right" mr="4px">
            <BalanceValue tokenInfo={token} />
            {token?.balanceUSD !== undefined && d(token?.balanceUSD).gt(0) && (
              <Text mt="6px" fontSize="12px">
                {symbolDataDisplayProcessing(token?.balanceUSD)}
              </Text>
            )}
          </Box>
        )}

        {/* {currentTab == 'Imported' && !isShowImportBtn ? ( */}
        {!isHideDelIcon && !(token?.is_verified === undefined ? token?.is_trusted : token?.is_verified) ? (
          <Icon
            marginLeft="2px"
            xlinkHref="#icon-icon_del"
            svgHover="primary"
            onClick={e => {
              e.stopPropagation()
              delImportTokenList([token])
            }}
          />
        ) : null}

        {userCollectObj[token.coin_type] && isShowCollectListBox ? (
          <Icon
            marginLeft="4px"
            xlinkHref="#icon-icon_star_sel"
            svgFill="primary"
            svgHover="primary"
            // cursor={canCollect ? 'pointer' : 'no-drop'}
            onClick={e => {
              e.stopPropagation()
              // if (!canCollect) {
              //   return false
              // }
              delUserCollectToken(token)
            }}
          />
        ) : !isShowImportBtn && isShowCollectListBox ? (
          <Icon
            marginLeft="4px"
            xlinkHref="#icon-icon_star"
            variant="primary"
            onClick={e => {
              e.stopPropagation()
              addUserCollectToken(token)
            }}
          />
        ) : null}
        <CetusTooltip
          placement="top"
          tooltip={
            <Text fontSize="12px" color="text_caption">
              Coin Info
            </Text>
          }
        >
          <Center
            as="button"
            ml="4px"
            onClick={(e: any) => {
              cancelBubble(e)
              if (onGoplusTokenClick) {
                onGoplusTokenClick(token)
              } else {
                e.target.blur()
                setIsShowGoplusModal(true)
                setTokenModalOpacity(0)
              }
            }}
          >
            <Icon xlinkHref="#icon-icon_tips" />
          </Center>
        </CetusTooltip>
      </Flex>
      {isShowGoplusModal && (
        <TokenGoplusModal
          tokenInfo={token}
          isOpen={isShowGoplusModal}
          onBack={() => {
            setIsShowGoplusModal(false)
            setTokenModalOpacity(1)
          }}
          onClose={onClose}
        />
      )}
    </Flex>
  )
}

export default TokenItem
