import useTokenSelectStore from '@cetus/stores/src/useTokenSelectStore'
import { Icon, NoData } from '@cetus/ui-kit'
import { Flex, Text, Tooltip } from '@chakra-ui/react'
import { useMemo } from 'react'

type NoDataContentProps = {
  keyword: string
  currentTab: string
  isHaveImg: boolean
}

const NoDataContent = ({ keyword, currentTab, isHaveImg }: NoDataContentProps) => {
  const { filterTokenLoading } = useTokenSelectStore()

  const noDataText = useMemo(() => {
    if (keyword) {
      return 'No tokens found'
    } else if (!keyword && currentTab === 'Imported') {
      return 'No Imported Tokens'
    } else if (!keyword && currentTab === 'Watchlist') {
      return 'No date avalaible'
    }
  }, [keyword, currentTab])

  const noDataChildren = useMemo(() => {
    return keyword ? (
      <Flex alignItems="center">
        <Text height="20px" lineHeight="20px">
          Or you can enter Coin Type
        </Text>
        <Tooltip label="The unique id of coin" placement="bottom">
          <button>
            <Icon marginLeft="4px" xlinkHref="#icon-icon_tips" svgW="20px" svgH="20px" />
          </button>
        </Tooltip>
      </Flex>
    ) : null
  }, [keyword])

  return !filterTokenLoading ? (
    <NoData type="nodata" text={noDataText} children={noDataChildren} background="none" noBorder={true} noImg={!isHaveImg} />
  ) : null
}

export default NoDataContent
