import { Token } from '@cetus/types'
import { BackButton } from '@cetus/ui-kit'
import { HStack, Modal, ModalBody, ModalCloseButton, ModalContent, ModalHeader } from '@chakra-ui/react'
import SingleTokenInfo from '../SingleTokenInfo'
import TokenGoplusCard from './TokenGoplusCard'

export default function TokenGoplusModal({
  tokenInfo,
  onClose,
  onBack,
  isOpen
}: {
  tokenInfo: Token
  isOpen: boolean
  onClose: () => void
  onBack: () => void
}) {
  return (
    <Modal autoFocus={false} returnFocusOnClose={false} isOpen={isOpen} onClose={onClose} isCentered>
      <ModalContent overflow="hidden">
        {/* 标题 */}
        <ModalHeader>
          <HStack gap="12px">
            <BackButton onClick={onBack} />
            <SingleTokenInfo portal={false} token={tokenInfo} imgBoxStyle={{ w: '28px', h: '28px' }} />
          </HStack>
        </ModalHeader>
        <ModalCloseButton onClick={() => onClose()} />
        <ModalBody p="0">
          <TokenGoplusCard tokenInfo={tokenInfo} />
        </ModalBody>
      </ModalContent>
    </Modal>
  )
}
