import useTokenSelect from '@/hook/useTokenSelect'
import useTokenPrice from '@cetus/hooks/src/useTokenPrice'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import useTokenBalanceStore from '@cetus/stores/src/tokenBalance'
import useTokenSelectStore from '@cetus/stores/src/useTokenSelectStore'
import { Token } from '@cetus/types'
import { isAvailableObject } from '@cetus/utils'
import { extractStructTagFromType } from '@cetusprotocol/cetus-sui-clmm-sdk'
import { Box, Button, Center, Flex, Text } from '@chakra-ui/react'
import { useEffect, useMemo } from 'react'
import Loading from '../Loading'
import NoDataContent from './NoDataContent'
import TokenItem from './TokenItem'
import TokenListVirtualized from './TokenListVirtualized'

type TokenListProps = {
  onSelectToken: (tokenInfo: Token) => void
  keyword: string
  currentLabel: string
  currentTab: string
  lastSelectedToken: string
  isDisableSelectTokenItem: boolean
  tokenList: Token[]
  isShowCollectListBox: boolean
  isShowTokenListTab: boolean
  noDataHaveImg?: boolean
  bg?: string
  tokenListMaxHeight?: string
  noClickImportItem?: boolean
  onClose: () => void
  onGoplusTokenClick?: (tokenInfo: Token) => void
}

export default function TokenList(props: TokenListProps) {
  const {
    onSelectToken,
    keyword,
    currentLabel,
    currentTab,
    lastSelectedToken,
    isDisableSelectTokenItem,
    tokenList,
    isShowCollectListBox,
    isShowTokenListTab,
    onGoplusTokenClick,
    onClose,
    noDataHaveImg = true,
    noClickImportItem = false,
    bg,
    tokenListMaxHeight
  } = props

  const { delUserCollectToken, addUserCollectToken, addImportTokenList, delImportTokenList } = useTokenSelect()
  const { filterTokenList, filterTokenLoading, userCollectObj, isShowImportBtn } = useTokenSelectStore()

  const { isApp } = useWindowWidth()

  const shouldUseVirtualization = tokenList?.length * 72 > Number(tokenListMaxHeight?.replace('px', ''))
  const shouldPaddingleft = tokenList?.length > 6

  const tokenListHeight = useMemo(() => {
    if (keyword && tokenListMaxHeight) {
      return Number(tokenListMaxHeight?.replace('px', '')) + 80 + 'px'
    }
    return tokenListMaxHeight
  }, [tokenListMaxHeight, keyword])

  const { tokenBalanceObj } = useTokenBalanceStore()
  const { fetchTokenPrices } = useTokenPrice()
  useEffect(() => {
    console.log('tokenBalanceObj', tokenBalanceObj)
    if (isAvailableObject(tokenBalanceObj)) {
      const coinTypes =
        tokenList?.filter((item: any) => item?.coin_type && tokenBalanceObj[extractStructTagFromType(item?.coin_type)?.full_address]) || []
      const uniqueTokens = coinTypes.map((item: Token) => item?.coin_type)
      console.log('🚀 ~ useEffect ~ uniqueTokens:', coinTypes, uniqueTokens)
      fetchTokenPrices(uniqueTokens)
    }
  }, [tokenList?.length])

  return (
    <Box maxHeight={tokenListHeight ? tokenListHeight : '400px'} pl={shouldPaddingleft ? '4px' : '0px'} overflow="auto">
      {isShowImportBtn && keyword && filterTokenList?.length > 0 && (
        <Flex padding="0 16px 12px" marginTop="10px">
          <Box p="16px" bg="primary_yellow_opacity.10" borderRadius="12px">
            <Text color="primary_yellow" align="left" fontSize="12px" lineHeight="20px">
              The token is not on the Frequently Traded List. Please note that anyone can create a token on Sui blockchain with any name, including
              creating fake versions of existing tokens or tokens that claim to represent projects that do not have a token. Always conduct your own
              research before trading.
            </Text>
          </Box>
        </Flex>
      )}
      {filterTokenLoading ? (
        <Box height="400px" position="relative" borderRadius="16px" overflow="hidden">
          <Loading positionStyle="absolute" bg={bg} />
        </Box>
      ) : tokenList?.length > 0 ? (
        shouldUseVirtualization ? (
          <TokenListVirtualized
            onGoplusTokenClick={onGoplusTokenClick}
            list={tokenList}
            onSelectToken={onSelectToken}
            isDisableSelectTokenItem={isDisableSelectTokenItem}
            lastSelectedToken={lastSelectedToken}
            currentTab={currentTab}
            isShowCollectListBox={isShowCollectListBox}
            isShowTokenListTab={isShowTokenListTab}
            delImportTokenList={delImportTokenList}
            addUserCollectToken={addUserCollectToken}
            delUserCollectToken={delUserCollectToken}
            userCollectObj={userCollectObj}
            width={isApp ? 360 : 482}
            height={tokenListHeight}
            onClose={onClose}
          />
        ) : (
          <Box maxHeight={tokenListHeight ? tokenListHeight : '400px'} overflow="auto">
            {tokenList?.map((token, index) => (
              <TokenItem
                onGoplusTokenClick={onGoplusTokenClick}
                key={token.coin_type}
                token={token}
                onSelectToken={(ele: any) => {
                  onSelectToken(ele)
                }}
                isNotAllowClick={!!(noClickImportItem && isShowImportBtn && keyword && filterTokenList?.length > 0)}
                isShowCollectListBox={isShowCollectListBox}
                isShowTokenListTab={isShowTokenListTab}
                isDisableSelectTokenItem={isDisableSelectTokenItem}
                isHideDelIcon={!!(isShowImportBtn && keyword && filterTokenList?.length > 0)}
                lastSelectedToken={lastSelectedToken}
                currentTab={currentTab}
                delImportTokenList={delImportTokenList}
                addUserCollectToken={addUserCollectToken}
                delUserCollectToken={delUserCollectToken}
                userCollectObj={userCollectObj}
                onClose={onClose}
              />
            ))}
          </Box>
        )
      ) : (
        // )
        <NoDataContent currentTab={currentTab} keyword={keyword} isHaveImg={noDataHaveImg} />
      )}
      {isShowImportBtn && keyword && (
        <Center p="16px 0">
          <Button
            w="120px"
            h="36px"
            fontSize="14px"
            onClick={() => {
              addImportTokenList([filterTokenList[0]])
              onSelectToken(filterTokenList[0])
            }}
          >
            Import
          </Button>
        </Center>
      )}
    </Box>
  )
}
